import ICEPath from '../ICEPath';
import ICEVector from '../ICEVector';
/**
 * @class ICERect 矩形
 * @author 大漠穷秋<damoqiongqiu@126.com>
 */
class ICERect extends ICEPath {
    constructor(props = { width: 10, height: 10 }) {
        super(props);
    }
    calcDots() {
        let point1 = new ICEVector({ x: 0, y: 0 });
        let point2 = new ICEVector({ x: this.state.width, y: 0 });
        let point3 = new ICEVector({ x: this.state.width, y: this.state.height });
        let point4 = new ICEVector({ x: 0, y: this.state.height });
        this.dots = [point1, point2, point3, point4];
        return this.dots;
    }
}
export default ICERect;
