/**
 * 二维向量，笛卡尔坐标系。
 * @author 大漠穷秋<damoqiongqiu@126.com>
 */
class ICEVector {
    constructor(v = { x: 0, y: 0 }) {
        this.x = 0;
        this.y = 0;
        this.x = v.x;
        this.y = v.y;
    }
    /**
     * 向量相加，生成新的向量对象。
     * @param v
     * @returns
     */
    add(v) {
        let newVector = new ICEVector();
        newVector.x = this.x + v.x;
        newVector.y = this.y + v.y;
        return newVector;
    }
    /**
     * 向量相减，生成新的向量。
     * @param v
     * @returns
     */
    sub(v) {
        let newVector = new ICEVector();
        newVector.x = this.x - v.x;
        newVector.y = this.y - v.y;
        return newVector;
    }
    mul(v) {
        //TODO:实现向量叉乘
        return this;
    }
    dot(v) {
        //TODO:实现向量点乘
        return this;
    }
}
export default ICEVector;
