import ICEEvent from './ICEEvent';
/**
 * Canvas 内部的对象默认没有事件机制，模仿 W3C 定义的 EventTaregt 接口，为 Canvas 内部的图形添加事件机制。
 * 部分API 名称模仿 jQuery。
 * @see https://developer.mozilla.org/en-US/docs/Web/API/EventTarget
 * @author 大漠穷秋<damoqiongqiu@126.com>
 */
class EventTarget {
    constructor(props) {
        this.listeners = {};
        this.suspendedEventNames = [];
        //TODO:some props?
    }
    on(eventName, fn) {
        //TODO:support event array using isArrayLikeObject from lodash.
        if (!this.listeners[eventName]) {
            this.listeners[eventName] = [];
        }
        if (!this.listeners[eventName].includes(fn)) {
            this.listeners[eventName].push(fn);
        }
    }
    off(eventName, fn) {
        if (!this.listeners[eventName])
            return false;
        if (!fn)
            this.listeners[eventName] = []; //clear all callbacks.
        if (!this.listeners[eventName].includes(fn))
            return false;
        this.listeners.splice(this.listeners[eventName].findIndex(fn), 1);
        return true;
    }
    once(eventName, fn) {
        this.on(eventName, (evt) => {
            this.off(eventName, fn);
            fn.apply(evt);
        });
    }
    trigger(eventName, originalEvent) {
        if (!this.listeners[eventName])
            return false;
        if (this.suspendedEventNames.includes(eventName))
            return false;
        //DOM 事件和代码触发的事件都会被转换成 ICEEvent
        let iceEvent;
        if (originalEvent) {
            iceEvent = new ICEEvent(originalEvent);
            iceEvent.originalEvent = originalEvent;
        }
        else {
            iceEvent = new ICEEvent({
                type: eventName,
                timeStamp: new Date().getTime(),
            });
        }
        this.listeners[eventName].forEach((cb) => {
            cb(iceEvent);
        });
        return true;
    }
    suspend(eventName, fn) {
        if (eventName && !this.suspendedEventNames.includes(eventName)) {
            this.suspendedEventNames.push(eventName);
        }
        if (fn) {
            fn.prototype.suspended = true;
        }
    }
    resume(eventName, fn) {
        if (eventName && this.suspendedEventNames.includes(eventName)) {
            this.suspendedEventNames.splice(this.suspendedEventNames.findIndex(eventName), 1);
        }
        if (fn) {
            fn.prototype.suspended = false;
        }
    }
    purge() {
        this.listeners = {};
    }
}
//模拟 W3C 的 EventTarget 接口
EventTarget.prototype.addEventListener = EventTarget.prototype.on;
EventTarget.prototype.removeEventListener = EventTarget.prototype.off;
EventTarget.prototype.dispatchEvent = EventTarget.prototype.trigger;
export default EventTarget;
