import { mat3 } from 'gl-matrix';
import ICE_CONSTS from '../ICE_CONSTS';
import ICEBoundingBox from './ICEBoundingBox';
import ICEComponent from './ICEComponent';
/**
 * @class ICEPath
 * 路径由一系列点构成。
 * @author 大漠穷秋<damoqiongqiu@126.com>
 */
class ICEPath extends ICEComponent {
    constructor(props = {}) {
        super(props);
        this.dots = [];
        this.path2D = new Path2D();
        this.dots = this.state.dots ? this.state.dots : [];
        this.state = { ...this.state, dots: this.dots };
    }
    /**
     * @overwrite
     * @param newState
     */
    setState(newState) {
        super.setState(newState);
        this.dots = [...this.state.dots];
    }
    /**
     * @param ctx CanvasRenderingContext2D
     */
    render(ctx) {
        this.isRendering = true;
        this.trigger(ICE_CONSTS.BEFORE_RENDER);
        ctx.strokeStyle = '';
        ctx.fillStyle = 'red';
        ctx.beginPath();
        this.createPath();
        ctx.stroke(this.path2D);
        ctx.fill(this.path2D);
        ctx.restore();
        this.trigger(ICE_CONSTS.AFTER_RENDER);
        this.isRendering = false;
    }
    createPath() {
        this.calcDots();
        this.applyTransform();
        this.doCreatePath();
        return this.path2D;
    }
    /**
     * 全部相对于 [0,0] 点，在 [0,0] 点计算完成之后再用变换矩阵左乘
     * @returns
     */
    calcDots() {
        return this.state.dots;
    }
    doCreatePath() {
        this.path2D = new Path2D();
        for (let i = 0; i < this.state.dots.length; i++) {
            const vertex = this.state.dots[i];
            if (i === 0) {
                this.path2D.moveTo(vertex.x, vertex.y);
            }
            else {
                this.path2D.lineTo(vertex.x, vertex.y);
            }
        }
        this.path2D.closePath();
        return this.path2D;
    }
    applyTransform() {
        let matrix = this.composeMatrix();
        this.state.dots.forEach((v) => {
            let transformed = mat3.mul([], matrix, [v.x, v.y, 1, 0, 0, 0, 0, 0, 0]);
            v.x = Math.floor(transformed[0]); //像素点使用整数
            v.y = Math.floor(transformed[1]); //像素点使用整数
        });
    }
    getOriginalBoundingBox() {
        let minX = 0;
        let minY = 0;
        let maxX = 0;
        let maxY = 0;
        for (let i = 0; i < this.state.dots.length; i++) {
            let v = this.state.dots[i];
            if (i === 0) {
                minX = v.x;
                maxX = v.x;
                minY = v.y;
                maxY = v.y;
            }
            else {
                if (v.x < minX) {
                    minX = v.x;
                }
                if (v.x > maxX) {
                    maxX = v.x;
                }
                if (v.y < minY) {
                    minY = v.y;
                }
                if (v.y > maxY) {
                    maxY = v.y;
                }
            }
        }
        return new ICEBoundingBox({ left: minX, top: minY, width: Math.floor(maxX - minX), height: Math.floor(maxY - minY) });
    }
    getMinBoundingBox() {
        const temp = new ICEBoundingBox({ left: 0, top: 0, width: this.state.width, height: this.state.height }); //width, height 可能会因为动画效果发生变化
        const mat = this.state.transformMatrix;
        return temp.transform(mat);
    }
}
export default ICEPath;
