import { mat3 } from 'gl-matrix';
import get from 'lodash/get';
import EventTarget from '../event/EventTarget';
/**
 * @class ICEComponent
 * Canvas 内部所有组件的根类
 * TODO:全部采用 Path2D 的方式绘制图形，不采用 CanvasRenderingContext2D 上定义的绘制图形的方法
 * TODO:定义图形对象的完整数据结构，包含path/style/animation/transformation
 * TODO:动画运行过程中不允许选中，不能进行交互
 * @abstract
 * @author 大漠穷秋<damoqiongqiu@126.com>
 */
class ICEComponent extends EventTarget {
    constructor(props = {}) {
        super(props);
        //FIXME:生成随机ID有问题，FIX
        //sha256(Math.random() * 100000000).toString();
        this.id = 'ICE_' + Math.floor(Math.random() * 10000000000);
        this.zIndex = 0;
        this.silent = false; //silent=true 将不会触发任何事件
        this.isRendering = false;
        //Default animations.
        this.animations = {};
        //Default transform.
        this.transform = {
            translate: [0, 0],
            scale: [1, 1],
            skew: [0, 0],
            rotate: 0,
        };
        //3阶单位矩阵，请勿修改此矩阵的值
        this.mat3Identity = [1, 0, 0, 0, 1, 0, 0, 0, 1];
        /**
         * Default props.
         * Do NOT modify props, use state for instead.
         * 在二维画布上，可视对象一定存在 x/y/width/height 这4个属性。
         */
        this.props = {
            left: 0,
            top: 0,
            animations: this.animations,
            transform: this.transform,
            transformMatrix: [...this.mat3Identity],
        };
        this.state = { ...this.props };
        this.props = { ...this.props, ...props };
        this.state = { ...this.props };
        this.animations = this.props.animations;
        this.transform = this.props.transform;
        // this.id = 'ICE_' + sha256(Math.random() * 100000000).toString();
        this.zIndex++; //TODO:fix zIndex
    }
    setState(newState) {
        this.state = { ...this.state, ...newState };
    }
    /**
     * @param ctx CanvasRenderingContext2D
     */
    applyStrokeStyle(ctx) {
        console.log('apply stroke style...');
    }
    /**
     * @param ctx CanvasRenderingContext2D
     */
    applyFillStyle(ctx) {
        console.log('apply fill style...');
    }
    /**
     * 复合变换矩阵
     * 矩阵变换顺序：错切->旋转->缩放->平移
     */
    composeMatrix() {
        //skew
        let skewMat = [...this.mat3Identity];
        if (get(this, 'transform.skew')) {
            skewMat[3] = get(this, 'transform.skew.0');
            skewMat[1] = get(this, 'transform.skew.1');
        }
        //rotate
        let rotateMat = [...this.mat3Identity];
        if (get(this, 'transform.rotate')) {
            let angle = get(this, 'transform.rotate');
            let radian = (angle * Math.PI) / 180;
            rotateMat[0] = Math.cos(radian);
            rotateMat[1] = Math.sin(radian);
            rotateMat[3] = -Math.sin(radian);
            rotateMat[4] = Math.cos(radian);
        }
        //scale
        let scaleMat = [...this.mat3Identity];
        if (get(this, 'transform.scale')) {
            scaleMat[0] = get(this, 'transform.scale.0');
            scaleMat[4] = get(this, 'transform.scale.1');
        }
        //translate [[1,0,0][0,1,0][dx,dy,1]]
        let translateMat = [...this.mat3Identity];
        translateMat[6] = this.state.left;
        translateMat[7] = this.state.top;
        if (get(this, 'transform.translate')) {
            translateMat[6] += get(this, 'transform.translate.0');
            translateMat[7] += get(this, 'transform.translate.1');
        }
        //依次左乘
        let result = mat3.mul([], rotateMat, skewMat);
        result = mat3.mul([], scaleMat, result);
        result = mat3.mul([], translateMat, result);
        //缓存当前的变换矩阵
        this.state.transformMatrix = [...result];
        return result;
    }
    /**
     * TODO:改成 abstract?
     * @returns
     */
    toJSON() {
        return '{}';
    }
    /**
     * TODO:改成 abstract
     * @returns
     */
    fromJSON(jsonStr) {
        return {};
    }
}
export default ICEComponent;
