import ICEPath from '../ICEPath';
/**
 * @class ICELine 直线
 * @author 大漠穷秋<damoqiongqiu@126.com>
 */
class ICELine extends ICEPath {
    constructor(props = { startPoint: [0, 0], endPoint: [10, 10] }) {
        super(props);
        this.lineType = 'solid'; //solid, dashed
        this.arrow = 'none'; //start, end, both, none
        this.startPoint = [];
        this.endPoint = [];
        this.state = { ...this.props, lineType: this.props.lineType, arrow: this.props.arrow };
        this.startPoint = [...this.props.startPoint];
        this.endPoint = [...this.props.endPoint];
    }
    createPath() {
        this.path2D = new Path2D();
        this.path2D.moveTo(this.state.startPoint[0], this.state.startPoint[1]);
        this.path2D.lineTo(this.state.endPoint[0], this.state.endPoint[1]);
        return this.path2D;
    }
    //TODO:直线、曲线只支持平移变换，不支持其它变换形式
    applyTransform() {
        throw new Error('Method not implemented.');
    }
}
export default ICELine;
