import ICEImage from '../src/graphic/ICEImage';
import ICEText from '../src/graphic/ICEText';
import ICELine from '../src/graphic/line/ICELine';
import ICEIsogon from '../src/graphic/shape/ICEIsogon';
import ICERect from '../src/graphic/shape/ICERect';
import ICEStar from '../src/graphic/shape/ICEStar';

ICE.init('canvas-1');

let rect = new ICERect({
  left: 0,
  top: 0,
  width: 100,
  height: 100,
  animations: {
    left: { from: 0, to: 500, duration: 1000, easing: 'easeInQuad' },
    top: { from: 0, to: 200, duration: 3000 },
    width: { from: 100, to: 200, duration: 5000 },
    height: { from: 100, to: 200, duration: 5000 },
  },
  transform: {
    translate: [10, -10],
    scale: [0.3, 0.5],
    skew: [0.2, 0],
    rotate: 30,
  },
});
let line = new ICELine({ startPoint: [50, 50], endPoint: [350, 350] });
let img = new ICEImage();

rect.on('click', (evt) => {
  // console.log('rect');
});

line.on('click', (evt) => {
  // console.log('line');
});

img.on('click', (evt) => {
  // console.log('image');
});

ICE.addChild(rect);
ICE.addChild(line);
ICE.addChild(img);

//正三角形
let isogon3 = new ICEIsogon({
  left: 300,
  top: 0,
  radius: 50,
  edges: 3,
});
ICE.addChild(isogon3);

//正五边形
let isogon5 = new ICEIsogon({
  left: 500,
  top: 0,
  radius: 50,
  edges: 5,
});
ICE.addChild(isogon5);

//正十五边形
let isogon15 = new ICEIsogon({
  left: 700,
  top: 0,
  radius: 50,
  edges: 15,
});
ICE.addChild(isogon15);

//正五角星
let star1 = new ICEStar({
  left: 700,
  top: 100,
  radius: 50,
  edges: 5,
  transform: {
    translate: [10, -10],
    scale: [0.3, 0.5],
    skew: [0.2, 0],
    rotate: 30,
  },
});
star1.on('click', (evt) => {
  // console.log(star1.getOriginalBoundingBox());
});
ICE.addChild(star1);

let itext = new ICEText({
  text: 'Hello World',
  transform: {
    translate: [10, -10],
    scale: [0.3, 0.5],
    skew: [0.2, 0],
    rotate: 30,
  },
});
ICE.addChild(itext);

