import { mat3 } from 'gl-matrix';
/**
 * @class ICEBoundingBox 边界矩形
 *
 * 边界矩形总是绘制在全局坐标系中。
 * @see https://developer.mozilla.org/en-US/docs/Web/API/DOMRect
 * @author 大漠穷秋<damoqiongqiu@126.com>
 */
class ICEBoundingBox {
    constructor(props = {}) {
        this.left = 0;
        this.top = 0;
        this.right = 0;
        this.bottom = 0;
        this.width = 0;
        this.height = 0;
        this.originalPoints = [];
        this.transformedPoints = [];
        let { top = 0, left = 0, width = 0, height = 0 } = props;
        this.top = top;
        this.left = left;
        this.width = width;
        this.height = height;
        this.right = left + width;
        this.bottom = top + height;
        //四点法，原始边界矩形的4个顶点
        this.originalPoints = [
            [this.left, this.top, 1],
            [this.left + this.width, this.top, 1],
            [this.left + this.width, this.top + this.height, 1],
            [this.left, this.top + this.height, 1],
        ];
        this.transformedPoints = [
            [this.left, this.top, 1],
            [this.left + this.width, this.top, 1],
            [this.left + this.width, this.top + this.height, 1],
            [this.left, this.top + this.height, 1],
        ];
    }
    /**
     * 边界矩形合并
     * @param target
     * @returns
     */
    union(target) {
        let { top, left, right, bottom, width, height } = this;
        top = target.top < top ? target.top : top;
        left = target.left < left ? target.left : left;
        bottom = target.bottom > bottom ? target.bottom : bottom;
        right = target.right > right ? target.right : right;
        width = right - left;
        height = bottom - top;
        return new ICEBoundingBox({ top, left, width, height });
    }
    transform(mat) {
        let result = [];
        for (let i = 0; i < this.originalPoints.length; i++) {
            result.push(mat3.mul([], mat, this.originalPoints[i]));
        }
        this.transformedPoints = result;
        return this;
    }
}
export default ICEBoundingBox;
