import {ICE_CONSTS} from '../ICE_CONSTS';
import ICEComponent from './ICEComponent';
/**
 * @class ICEImage
 * TODO:支持以下几种图片类型：jpg/jpeg/png/gif
 * TODO:ICEImage 来源的几种方式
 * @author 大漠穷秋<damoqiongqiu@126.com>
 */
class ICEImage extends ICEComponent {
    constructor(props = {}) {
        super(props);
    }
    /**
     * @param ctx CanvasRenderingContext2D
     */
    render(ctx) {
        this.isRendering = true;
        this.trigger(ICE_CONSTS.BEFORE_RENDER);
        ctx.beginPath();
        let img = new Image();
        img.src = 'data:image/gif;base64,R0lGODlhCwALAIAAAAAA3pn/ZiH5BAEAAAEALAAAAAALAAsAAAIUhA+hkcuO4lmNVindo7qyrIXiGBYAOw==';
        ctx.drawImage(img, 300, 300);
        ctx.restore();
        this.isRendering = false;
        this.trigger(ICE_CONSTS.AFTER_RENDER);
    }
    applyTransform() {
        throw new Error('Method not implemented.');
    }
    getOriginalBoundingBox() {
        throw new Error('Method not implemented.');
    }
    getMinBoundingBox() {
        throw new Error('Method not implemented.');
    }
}
export default ICEImage;
